import React from 'react';
import { Container } from 'react-bootstrap';
import img1 from './Rectangle 88.svg';
import img2 from './Rectangle 89.svg';
import img3 from './Rectangle 90.svg';
import { Calculator } from '../Main/Calculator';
import { Contacts } from '../Questions/Contacts';

export const News = () => {
  const news = [
    {
      name: '   ',
      preview: '    , ,       ...',
      img: img1,
      date: '10  2023'
    },
    {
      name: '    ',
      preview: ' ,   ,    .   ...',
      img: img2,
      date: '9  2023'
    },
    {
      name: '  : ,    ',
      preview: '           .     ...',
      img: img3,
      date: '7  2023'
    }
  ]
  return (
    <section className='news'>
      <Container>
        <h4 className='news__title'>
           
        </h4>
        <div className='news__list'>
          {news.map(newsItem => (
            <div className='news__item' key={newsItem.name}>
              <img 
                src={newsItem.img}
                alt=""
                className='news__img'
              />
              <p className='news__date'>
                {newsItem.date}
              </p>
              <p className='news__name'>
                {newsItem.name}
              </p>
              <p className='news__preview'>
                {newsItem.preview}
              </p>
            </div>
          ))}
        </div>
        <h4 className='news__title'>
            
        </h4>
        <Calculator />
        <h4 className='news__title2'>
            
        </h4>
        <Contacts />
      </Container>
    </section>
  );
};

