import classNames from 'classnames';
import React from 'react';
import { Container } from 'react-bootstrap';
import { CleaningBlock } from './CleaningBlock';

export const Cleaning = () => {
  const [partOfAppartment, setPartOfAppartment] = React.useState('');

  const buttons = [
    '',
    '',
    '',
    ''
  ];

  return (
    <section className='cleaning'>
      <Container>
        <h3 className='cleaning__title'>
             
        </h3>
        <div className='cleaning__buttons'>
          {buttons.map(button => (
            <button 
              className={classNames('cleaning__button', {
                'cleaning__button--active': button === partOfAppartment
              })}
              onClick={(e) => {
                setPartOfAppartment(button)
              }}
              key={button}
            >
              {button}
            </button>
          ))}
        </div>
        <div className='cleaning__scheme'>
        <div className='cleaning__schemeBlock mb-4'>
          <CleaningBlock 
            block={{
              txt: ' , , , , ',
              width: '321',
              height: '108',
              y: 0,
              left: 5,
            }}
          />
          <CleaningBlock 
            block={{
              txt: '  ',
              width: '261',
              height: '80',
              y: -50,
              left: 5,
            }}
          />
        </div>
        <div className='cleaning__schemeBlock'>
          <CleaningBlock 
            block={{
              txt: '  ',
              width: 261,
              height: 80,
              y: 0,
              left: 10,
              additional: '( -  1..,  -  ,   . )'
            }}
          />
        </div>
        <div className='cleaning__schemeBlock'>
          <CleaningBlock 
            block={{
              txt: ' ',
              width: 230,
              height: 80,
              y: -30,
              left: 55,
            }}
          />
        </div>
        <div className='cleaning__schemeBlock'>
          <CleaningBlock 
            block={{
              txt: '    ',
              width: 361,
              height: 80,
              y: 26,
              left: 3,
            }}
          />
          <CleaningBlock 
            block={{
              txt: '     ',
              width: 321,
              height: 80,
              y: 66,
              left: 1,
            }}
          />
        </div>
        <div className='cleaning__schemeBlock'>
          <CleaningBlock 
            block={{
              txt: '   ',
              width: 321,
              height: 80,
              y: 116,
              left: 1,
            }}
          />
        </div>
        </div>
      </Container>
    </section>
  );
};

