import React from 'react';
import { OfferButton } from './OfferButton';
import { Room } from './Room';

export const Offers = () => {
  const [discount, setDiscount] = React.useState(20);
  const buttons = [
    {
      name: '  ',
      discount: 20,
    },
    {
      name: '   ',
      discount: 15,
    },
    {
      name: '  ',
      discount: 10,
    },
    {
      name: ' ',
      discount: 0,
    }
  ];

  const rooms = [
    {
      name: '',
      price: 143.90,
      description: '    , ,    '
    },
    {
      name: '',
      price: 172.70,
      description: '    , ,    '
    },
    {
      name: '',
      price: 201.50,
      description: '    , ,    '
    }
  ];

  return (
    <div className='offers'>
    <div className='offers__buttons'>
      {buttons.map(button => (
        <OfferButton 
          button={button}
          discount={discount}
          setDiscount={setDiscount}
          key={button.name}
        />
      ))}
    </div>
    <div className='offers__rooms'>
      {rooms.map(room => (
        <Room 
          room={room} 
          discount={discount}
          key={room.name}
        />
      ))}
    </div>
    </div>
  );
};

